// 
export const catchAsync =
  (fn: any) => async (values?: unknown, success?: (data?: any) => void, error?: (content: string) => void) => {
    // success errorsuccesserror
    try {
      const response = await fn(values);
      if (success) success(response.data);
      return Promise.resolve(response.data);
    } catch (err: any) {
      // 
      if (error) {
        // 
        error(err.data.message);
        return new Promise(() => {
          /* pass */
        });
      }
    }
  };

// 
export const improvedCatchAsync =
  (fn: any) =>
  (
    values?: unknown, // valuesfn
    success?: (data?: any) => void,
    error?: (content: string) => void,
    effect?: () => void
  ) => {
    // success errorsuccesserror

    fn(values)
      .then((response?: any) => {
        if (success) success(response ? response.data : null);
      })
      .catch((err: any) => {
        if (error) {
          // 
          error(err.data.message);
        }
      })
      .finally(() => {
        if (effect) effect();
        return new Promise(() => {
          // 
        });
      });
  };
